.footer .site-map {
  padding: 1em 0;
  background-color: var(--dark-clr-2);
  text-align: center;
  color: white;
}

.footer .label {
  text-transform: capitalize;
  font-size: 1em;
  font-weight: 500;
  margin-bottom: .3em;
}

.footer .links {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: fit-content;
  column-gap: .7em;
  row-gap: .1em;
  margin: 0 auto;
}

.footer .links li{
  flex-basis: 33%;
  font-size: .8em;
}

.footer .links a {
  text-decoration: none;
  color: white;
  transition: color var(--duration);
}

.footer .links a:hover {
  color: var(--primary-clr);
}

.copyright {
  padding: .8em;
  background-color: var(--dark-clr-1);
  color: white;
  text-align: center;
  font-size: .7em;
}