.section-header {
  font-size: 2em;
  font-weight: 900;
  text-transform: capitalize;
  position: relative;
  padding-left: 15px;
}

.section-header::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: .3em;
  height: 100%;
  background-color: var(--primary-clr);
}