:root {
  --primary-clr: #FCD547;
  --primary-light-clr: #FFF5A3;
  --primary-dark-clr: #4E4219;
  --dark-clr-1: black;
  --dark-clr-2: #303234;
  --bg-clr-4: #FBFBFB;
  --bg-clr-1: #F6F6F6;
  --bg-clr-2: #F1F1F1;
  --bg-clr-3: #EDEDED;
  
  --section-padding: 5em;

  --border-radius-small: .25em;
  --border-radius-big: .4em;

  --duration: .3s;

  --margin-small: .7rem;
  --margin-big: 1rem;

  --box-shadow: 2px 2px 5px #80808054;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

img {
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Andada Pro', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}


.container {
  margin: 0 auto;
  padding: 0 30px;

}@media (min-width: 768px) {
  .container {
    width: 700px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 900px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1000px;
  }
}