.main-header {
  font-size: 2.3em;
  font-weight: 900;
  text-transform: capitalize;
}

.main-sub-header {
  font-size: 1.6em;
  font-weight: 700;
  text-transform: capitalize;
  color: #eee;
}

.main-text {
  font-size: 1em;
  color: #ccc;
  line-height: 1.7;
}


@media (min-width: 768px) {
  html {
    font-size: 17px;
  }
}
