.skills {
  padding: var(--section-padding) 0;
  background-color: var(--bg-clr-1);
}

.skills .section-header {
  margin-bottom: var(--margin-small);
}

.skills .section-sub-header {
  margin-bottom: var(--margin-big);
}

.skills .bars {
  margin-bottom: 2.5em;
}

.skill-bar {
  margin-bottom: var(--margin-small);
}

.skills .image {

  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius-big);
  box-shadow: var(--box-shadow);
}

.skills .img {
  max-width: 100%;
}

.skills .image::before {
  content: '';
  position: absolute;
  top: -4.5em;
  left: -2.5em;
  width: 6em;
  height: 9em;
  background-color: var(--primary-light-clr);
  transform: rotate(47.5deg);
}

.skills .image::after {
  content: '';
  position: absolute;
  top: -5.5em;
  left: 3.5em;
  width: 0.7em;
  height: 12em;
  background-color: var(--bg-clr-1);
  transform: rotate(47.5deg);
}


@media (min-width: 768px) {
  .skills .split {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
  }

  .skills .right {
    flex-basis: 60%;
  }

  .skills .bars {
    margin: 0;
  }
}