.input-field .label {
  text-transform: capitalize;
  margin-bottom: .2em;
}

.input-field .input {
  border: none;
  padding: .7em 1em;
  border-radius: var(--border-radius-small);
  color: var(--primary-dark-clr);
  width: 100%;
  box-shadow: 2px 2px 5px #80808014;
}

.input-field .input:focus {
  outline: none;
}

.input-field .input::placeholder {
  font-size: .9em;
}

.input-field .msg {
  font-family: inherit;
  resize: none;
  height: 120px;
}