header .links a {
  text-transform: capitalize;
  transition: color var(--duration);
  color: white;
  text-decoration: none;
}

header .active a,
header .links a:hover {
  color: var(--primary-clr);
}

header .icon {
  background-color: var(--primary-clr);
  color: white;
  font-size: 50px;
  padding: 5px;
  border-radius: 50%;
}


header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  header.wide .links {
    display: flex;
    justify-content: space-between;
    width: 400px;
    list-style: none;
  }
  
}



/* -----------  NARROW  ------------- */
@media (max-width: 767px) {
  
  header.narrow .bars {
    width: 40px;
    height: 33px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    cursor: pointer;
  }
  
  header.narrow .bars .bar {
    width: 100%;
    background-color: white;
  }
  
  header.narrow .bars .top {
    height: 5px;
  }
  
  header.narrow .bars .mid {
    height: 3.5px;
    width: 80%;
  }
  
  header .bars .bottom {
    height: 2px;
  }
  
  header.narrow .links {
    position: absolute;
    list-style: none;
    background: rgb(0 0 0);
    width: 140%;
    right: -140%;
    text-align: center;
    top: 150px;
    transition: .3s cubic-bezier(.25,.1,0,1.43);
  }
  
  header.narrow .links.show {
    right: -20%;
  }

 
  /* header.narrow .bars:hover + .links {
    right: -20%;
  } */
  
  
  header.narrow .links a {
    border-bottom: .1em solid;
    padding: 1em;
    display: block;
    font-size: 1.2em;
    font-weight: 500;
  }
}



