.card {
  background-color: white;
  border-radius: var(--border-radius-big);
  position: relative;
  overflow: hidden;
  box-shadow: var(--box-shadow);
  transition: transform var(--duration);
  backface-visibility: hidden;
  transform: translateZ(0);
}

.card .image {
  position: relative;
}

.card .image::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -0.15em;
  width: 100%;
  height: 0.2em;
  background-color: var(--primary-light-clr);
}

.card .img {
  max-width: 100%;
}

.card .label {
  margin: var(--margin-small);
  font-size: 1em;
  font-weight: 500;
  text-transform: capitalize;
}

.card .text {
  margin: 0 var(--margin-small) var(--margin-small) var(--margin-small);
  line-height: 1.5;
  color: var(--primary-dark-clr);
  font-size: 0.7em;
}

.card .link {
  width: 100%;
  background-color: var(--primary-light-clr);
  font-weight: 500;
  font-size: 0.85em;
  text-transform: capitalize;
  text-align: center;
  color: white;
}

.card .link a {
  text-decoration: none;
  color: inherit;
  display: block;
  padding: 0.5em 0;
}

@media (min-width: 768px) {
  .card:hover {
    transform: scale(1.02) translateZ(0);
  }

  .card .link {
    height: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff9d0;
    line-height: 0.9;
    opacity: 0;
    transition: opacity var(--duration);
    font-weight: 900;
    font-size: 6.5em;
  }

  .card:hover .link {
    opacity: 1;
  }

  .card .link a {
    transform: skew(15deg, -20deg) rotate(15deg);
  }
}
