.skill-bar {
  background-color: var(--dark-clr-2);
  padding: .3em .7em;
  color: white;
  display: flex;
  justify-content: space-between;
  border-radius: var(--border-radius-small);
  align-items: center;
  font-size: 1em;
}

.skill-bar .left {
  display: flex;
  align-items: center;
}

.skill-bar .label {
  text-transform: capitalize;
  margin-left: .7em;
  display: flex;
  line-height: 1em;
}

.skill-bar .icon {
  color: var(--primary-clr);
}

.skill-bar .percentage {
  line-height: 1em;
}