.contact-us {
  padding: var(--section-padding) 0;
  background-color: var(--bg-clr-3);
  position: relative;
  overflow: hidden;
}
.contact-us .section-header {
  margin-bottom: var(--margin-small);
}

.contact-us .section-sub-header {
  margin-bottom: var(--margin-big);
  z-index: 2;
  position: relative;
}

.contact-us .input-field {
  margin-bottom: var(--margin-small);
}

.contact-us form {
  position: relative;
  z-index: 2;
}


@media (min-width: 768px) {
  .contact-us form {
    width: 60%;
  }

  .contact-us .shape {
    position: absolute;
    width: 200px;
    height: 1146px;
    background-color: var(--primary-clr);
    top: -38%;
    right: 33%;
    transform: rotate(48deg);
  }
  
  .contact-us .shape::before {
    content: '';
    position: absolute;
    top: -100px;
    right: -35px;
    width: 20px;
    height: 1000px;
    background-color: var(--primary-light-clr);
  }
  
  
  .remove-shape {
    width: 50%;
    height: 166px;
    background-color: var(--bg-clr-3);
    position: absolute;
    top: 0;
    right: 0;
  }
  

}