.landing {
  background-color: var(--dark-clr-1);
  min-height: 100vh;
  color: white;
  padding: var(--section-padding) 0;
  overflow-x: hidden;
  position: relative;
  display: flex;
}

.landing .img {
  max-width: 100%;
}

.landing .main-header {
  margin-bottom: .2em;
}

.landing .main-sub-header {
  margin-bottom: .5em;
}

.landing .main-text {
  margin-bottom: 1em;
}

/* .landing .button {
  font-size: 1.2em;
} */


@media (min-width: 768px) {
  .landing .split {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    min-height: 100%;
  }
  .landing .img {
    transform: scale(1.2);
  }

  .landing .main-text {
    width: 90%;
  }
}

